export default {
	data() {
		return {
			num: 0,
			imgSrc: '',
			readed: 0,
			totalNum: 0
		};
	},
	onLoad( obj ) {
		this.num = obj.num;
		if ( obj.num == 1 ) {
			this.imgSrc = '../../static/index_bg1.jpg';
		} else {
			this.imgSrc = '../../static/index_bg2.jpg';
		}
	},
	methods:{
		onClick(type) {
			uni.navigateTo({
				url: `../info/info?num=${this.num}&type=${type}`
			})
		}
	}
}
